<template>
	<div id="spread-poster">
		<div class="content">
			<div class="img">
				<img :src="respon" alt="">
			</div>
			<div class="text-title">
				<van-icon name='smile'></van-icon>
				如何赚钱	
			</div>
			<div class="list-block">
				<ul>
					<li class="item-content van-hairline--bottom">
						<div class="item-media">第一步</div>
						<div class="item-inner">长按图片下载海报到相册,转发此海报给微信好友或分享到朋友圈；</div>
					</li>
					<li class="item-content van-hairline--bottom">
						<div class="item-media">第二步</div>
						<div class="item-inner">好友扫描您分享海报上的二维码,并且下单</div>
					</li>
					<li class="item-content van-hairline--bottom">
						<div class="item-media">第三步</div>
						<div class="item-inner" v-if="settle.balance_condition == 1">确认收货之后,您就可以获得佣金</div>
						<div class="item-inner" v-else>好友下单后,确认收货并评价之后,您就可以获得佣金</div>
					</li>
				</ul>
			</div>
			<div class="explain">
				<div class="text">
					说明: 分享后会带有独特的推荐码,您的好友访问之后,
					<template v-if="relate.become_child_limit == 1">
						如果好友没有被其他人推广，并且首次成功下单,
					</template>
					<template v-else-if="relate.become_child_limit == 2">
						首次成功下单
					</template>
					<template v-else-if="relate.become_child_limit == 3">
						如果好友没有被其他人推广，并且成功下单,
					</template>
					<template v-else-if="relate.become_child_limit == 4">
						成功下单,
					</template>
					<template v-if="relate.valid_period == 'once'">
						您将在{{settle.balance_condition == 1 ? '好友确认收货' : '好友确认收货并进行评价'}}后结算佣金，佣金仅限本次推广有效。
					</template>
					<template v-else-if="relate.valid_period == 'alltime'">
						您将在{{settle.balance_condition == 1 ? '好友确认收货' : '好友确认收货并进行评价'}}后结算佣金，他将成为您的下线直至其推广上线发生变化。
					</template>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				relate: {},
				settle: {},
				respon: ''
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'spread/poster/vue_index',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					this.relate = result.message.group_relate;
					this.settle = result.message.settle;
					this.respon = result.message.respon;
				})
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#spread-poster .content{
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
        overflow-y: scroll;
	}
	#spread-poster .content::-webkit-scrollbar {
        display: none;
    }
    #spread-poster .content .img{
	    height: 667px;
	    display: block;
    }
    #spread-poster .content .img img{
    	display: inline-block;
    	width: 100%;
    	height: 100%;
    }
    #spread-poster .content .text-title{
	    color: #888;
	    font-size: 14px;
	    margin: 10px 0;
	    padding: 0 14px;
	    line-height: 23px;
    }
    #spread-poster .content .text-title .van-icon{
    	font-size: 14px;
    }
    #spread-poster .content .list-block{
    	margin: 8px 0;
    	background: #fff;
    	font-size: 17px;
    }
    #spread-poster .content .list-block ul{
    	position: relative;
    	padding: 0;
    	margin: 0;
    	background: #fff;
    }
    #spread-poster .content .list-block ul .item-content{
    	margin: 0 15px;
	    display: -webkit-flex;
	    display: flex;
        justify-content: space-between;
    }
    #spread-poster .content .list-block ul .item-content .item-media{
    	font-size: 14px;
    	padding: 7px 0 8px;
    	flex-shrink: 0;
    	line-height: 29px;
    }
    #spread-poster .content .list-block ul .item-content .item-inner{
    	padding: 8px 0 7px 15px;
	    width: 100%;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
		font-size: 14px;
		color: #666;
		line-height: 29px;
    }
    #spread-poster .content .explain{
	    background-color: #fff;
	    text-align: center;
	    padding-bottom: 20px;
	    padding-top: 20px;
	    width: 100%;
    }
    #spread-poster .content .explain .text{
	    width: 87%;
	    background-color: #FF934A;
	    display: inline-block;
	    color: #fff;
	    padding: 6px;
	    font-size: 13px;
	    text-align: left;
	    line-height: 19px;
    }
</style>



